/* global SwaggerUIBundle, SwaggerUIStandalonePreset, axios */

const app = {
  el: '#app',
  data: {
    host: 'hexschoollivejs.herokuapp.com',
  },
  methods: {
    async getData() {
      try {
        const res = await axios.get('./data/swagger_output.json');

        res.data.host = app.data.host;
        this.swaggerFn(res.data);
      } catch (e) { /* empty */ }
    },
    swaggerFn(data) {
      window.ui = SwaggerUIBundle({
        spec: data,
        dom_id: app.el,
        deepLinking: true,
        presets: [
          SwaggerUIBundle.presets.apis,
          SwaggerUIStandalonePreset,
        ],
        plugins: [
          SwaggerUIBundle.plugins.DownloadUrl,
        ],
        layout: 'StandaloneLayout',
      });
    },
  },
  created() {
    this.methods.getData();
  },
};

window.onload = () => {
  app.created();
};
